  
  
/*********** general styles **************/
html , body{
  transition: 0.5s !important;
  scroll-behavior: smooth;
  overflow-x: hidden;
  padding : 0 !important;
  margin: 0 !important;
}
p{
  word-wrap: break-word !important;
}
a{
  text-decoration: none !important;
  color : unset !important;
}
.bold{
  font-weight: bold !important;
}
button , button:focus{
  text-transform: capitalize !important;
  outline: none !important;
  box-shadow : none !important;
}
.text-green{
  color : #3C7A50 !important;
}
.bg-green {
  background-color: #3C7A50 !important;
}
.bg-lightGreen{
  background-color: #E8F4E8 !important;
}
.bg-shineGreen{
  background-color: #E1F9C2 !important;
}
.btn-green{
  background-image: linear-gradient(#3C7A3C, #3C7A54) !important;
  border : 1px solid #3C7A3C !important; 
  color : white !important;
  padding: .5rem .75rem;
  border-radius: 0.25rem;
  outline: none !important;
}
.btn-green:hover , .btn-green:active {
  background-image: linear-gradient(#228522, #198542) !important;
  border : 1px solid #3C7A3C !important; 
  outline: none !important;
}
.btn-green-outline {
  background-color: white !important;
  border : 1px solid #3C7A3C !important;
  color : #3C7A3C !important;
  padding: .5rem 0.75rem;
  border-radius: 0.25rem;
  outline: none !important;
}
.text-grey{
  color: #8E8E8E !important;
}
.bg-lightgray{
  background-color: #F5F6FA !important;
}
.text-lightgrey{
  color : #A6A6A6 !important;
}
.border-green{
  border-bottom : 2px solid #3C7A3C !important;
  padding-bottom: 0.25rem;
}
.border-t-green{
  border-top : 3px solid #3C7A3C !important;
  padding-top: 0.25rem;
}
.border-list{
  border-bottom: 1px solid #dee2e6;
}
.border-list:last-child { border-bottom: none; }
.shadow {
  box-shadow: 1rem .5rem 3rem rgba(0,0,0,.07)!important;
}
.loading{
  position: fixed;
  z-index: 12345;
  height: 100vh;
  width: 100vw;
  text-align: center !important;
  top: 0px;
  left: 0px;
  padding-top: 15rem;
  background-color: rgba(0, 0, 0, 0.575);
}
.progress-bar{
  background-color: #3c7a50 !important;
}
.d-web{
  display: block !important;
}
.d-mob{
  display: none !important;
}
.d-sm-mob{
  display: none !important;
}
.d-all-mob{
  display: none !important;
}
.p-relative{
  position: relative !important;
}
.p-absolute{
  position: absolute !important;
}
.max-h-100-responsive{
  max-height: 100vh;
}
/* home */
.navbar-nav .nav-link:focus , 
.navbar-nav .nav-link:hover , 
.navbar-nav .nav-link:active{
  color: #3c7a3c !important;
}
.custom-nav .nav-link:focus , 
.custom-nav .nav-link:hover , 
.custom-nav  .nav-link:active{
    font-weight: bold;
    color: black  !important;
}
.form-control:focus {
    color: #495057;
    background-color: #fff;
    border-color: #4950573f !important;
    outline: 0;
    box-shadow: 0 0 0 0.2rem #28a7451f !important;
}
.title-line{
  position: relative;
  z-index: 0;
}
.title-line::after{
  content: "";
  height: 14px;
  width: 100%;
  background-color: #e5d99542;
  position: absolute;
  left: 0;
  top: 22px;
  z-index: -1;
}
.green-line{
  position: relative;
}
.green-line::after{
  content: "";
  height: 100%;
  width: 2px;
  background-color: #3C7A3C;
  position: absolute;
  left: -35px;
  top: 0;
}
/* end home */

/* login and register */
.bg-login{
  background-image:  url(../src/images/login/background.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
}
.bg-main{
  background-image:  url(../src/images/login/bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.p-bottom{
  position: absolute;
  bottom: 0;
  width: 100%;
}
 /* pin code */
 .styles_react-code-input__CRulA input{
   border : none !important;
   background-color: #E8F4E8 !important;
   margin: 0 0.3rem !important;
   border-radius: 5px !important;
   font-size: 27px !important;
 }
 .styles_react-code-input__CRulA input:focus{
    border : 1px solid #3C7A3C !important;
    color : #3C7A3C !important;
    background-color:white !important;
 }  

 /* instractor profile */
 .bg-muted{
   background-color: #E8F4E8 !important;
   border-color: #E9E9E9 !important;
   color: #333333 !important;
 }
 .MuiChip-root {
  color: #9e8e33 !important;
  font-weight: bolder;
  background-color: #e5d995 !important;
}
.active-header{
  color: #228522 !important;
}
/*student*/

input.search{
  background-image: url(/src/images/student/search-solid.svg);
  background-repeat: no-repeat;
  text-indent: 20px;  
 }

.search:focus{
  background-image: none;
}
.dropdown-item.active, .dropdown-item:active {
  background-color: white !important;
}
.dropdown-item.active p, .dropdown-item:active p{
  color: #198542 !important;
}
.dropdown-toggle::after{
  font-size: 28px;
  color: #606060;
}
.dropdown-shadow {
  box-shadow: 0.1rem 1em 2rem rgba(0,0,0,0.2)!important;
}
.MuiFormHelperText-root{
  text-align: end !important;
}
.custom-container{
  margin: 4rem;
}
.text-center-lg{
  text-align: center !important;
}

/************ responsive **************/
@media only screen  and (max-width : 540px){
     /* home */
    /* getInTouch */
    .d-web{
      display: none !important;
    }
    .d-mob{
      display: block !important;
    }
    .d-all-mob{
      display: block !important;
    }
    .text-center-lg{
      text-align: left !important;
    }
}
@media only screen  and (max-width : 1024px){
  .max-h-100-responsive {
    max-height: 100%;
  }
}
@media only screen  and (max-width : 414px){
  .text-center-responsive{
    text-align: center !important;
  }
  /* home */
  /* about */
  .green-line::after {
      left: -12px;
  }
  .text-center-responsive{
      text-align: center !important;
  }
  .custom-container {
    margin: 1rem !important;
}
}
@media only screen  and (max-width : 320px){
  .d-sm-mob{
    display: block !important;
  }
  .d-web{
    display: none !important;
  }
  .d-mob{
    display: none !important;
  }

}

